.wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.list {
  box-sizing: border-box;
  width: 500px;
  list-style: none;

  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text {
  display: flex;
  align-items: center;
  font-size: 22px;
  font-weight: 500;
  margin-right: 30px;
}

.tel {
  display: inline-block;
  font-size: 12px;
  font-weight: 400;
  margin-left: 40px;
}

.btn {
  display: inline-block;
  min-width: 50px;
  height: 40px;

  padding: 0 5px;

  border: 1px solid black;
  border-radius: 5px;

  color: black;
  background-color: transparent;

  cursor: pointer;
  transition: all ease-in 250ms;
}

.btn:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  color: white;
  background-color: black;
}
